.box-container {
    margin: 24px 14px 10px;
}
.box-container:hover .anticon {
    display: block;
}
.box-container>.icon-form {
    right: -5px;
    top: -5px;
}
.btn-add-box {
    text-align: center;
    margin-top: 10px;
    margin-bottom: 0;
    padding: 5px 0;
}
.box-item {
    position: relative;
    float: left;
}
.box-item:hover .box-item-delete {
    display: block;
}
.box-item.item-delete-flag .icon-operation {
    right: 15px;
}
.box-item.item-delete-flag .drop-container .icon-form {
    right: 5px;
    top: -7px;
}
.box-item .box-item-delete {
    position: absolute;
    right: 0;
    top: 3px;
    font-size: 14px;
    display: none;
}