.main-container .header-box {
    padding: 0;
    margin: 0;
    min-height: 50px;
}
.main-container .header-box .anticon.icon-operation{
    right: 0;
    top: 0;
}
.main-container .header-box .drop-container .anticon.icon-operation {
    right: 8px;
}
.main-container .header-box .ant-breadcrumb {
    padding-left: 24px;
}
.main-container .header-box .drop-container>div {
    text-align: left;
}
.main-container .header-box:hover .anticon.icon-operation {
    display: block;
}